import { Button, Modal, Paper, Typography, Grid } from '@mui/material';
import { Box } from '@mui/system';
export default function ModalReport({ report, modalReport, handleCloseModalReport }) {
	console.log(report);
	return (
		<Modal
			open={modalReport}
			onClose={handleCloseModalReport}
			aria-labelledby="modal-modal-title-edit"
			aria-describedby="modal-modal-description-edit"
		>
			<Box
				sx={{
					position: 'absolute',
					top: '50%',
					left: '50%',
					transform: 'translate(-50%, -50%)',
					width: '80%',
					height: '90%',
					bgcolor: 'white',
					boxShadow: 24,
				}}
			>
				<Box
					sx={{
						bgcolor: 'blue',
						color: 'white',
						p: 3,
					}}
				>
					<Typography
						id="modal-modal-title"
						variant="h5"
						component="h2"
						align="center"
						sx={{ fontWeight: 'bolder' }}
					>
						{/* Bo co s kin: {report.name} */}
					</Typography>
				</Box>
				<Grid container spacing={2}>
					<Grid item xs={9}>
						<div>xs=8</div>
					</Grid>
					<Grid item xs={3}>
						<div>xs=4</div>
					</Grid>
				</Grid>
			</Box>
		</Modal>
	);
}
