import {
	Button,
	FormControl,
	FormControlLabel,
	FormLabel,
	Grid,
	MenuItem,
	Modal,
	Radio,
	RadioGroup,
	TextField,
	Typography,
} from '@mui/material';
import { Box } from '@mui/system';
import Swal from 'sweetalert2';
import { useForm } from 'react-hook-form';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import enLocale from 'date-fns/locale/en-US';
import { useState } from 'react';
import { addNewEvent } from '../../../../api/eventApi';

export default function ModalAddEvent({ isOpen, handleClose, addNewEventState }) {
	const {
		register,
		handleSubmit,
		setValue,
		formState: { errors },
	} = useForm({ defaultValues: { repeatEvent: 0, dateEvent: new Date() } });
	const [dateModal, setDateModal] = useState(Date.now());
	const [repeatEvent, setRepeatEvent] = useState(0);
	const [dateOpenRegModal, setDateOpenRegModal] = useState(Date.now());
	const [dateEndRegModal, setDateEndRegModal] = useState(Date.now());
	const [typeEvent, setTypeEvent] = useState('');
	const handleAddNewEvent = (data) => {
		console.log(data);
		addNewEvent(data)
			.then((res) => {
				console.log(res.event);
				addNewEventState(res.event);
				handleClose();
				Swal.fire({
					position: 'center',
					icon: 'success',
					title: `Thm thnh cng ${res.event.name || res.event.length} s kin mi`,
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			})
			.catch((err) => {
				handleClose();
				Swal.fire({
					position: 'center',
					icon: 'error',
					title: 'C li xy ra vui lng th li',
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			});
	};
	return (
		<Modal
			open={isOpen}
			aria-labelledby="modal-modal-title"
			aria-describedby="modal-modal-description"
		>
			<Box
				sx={{
					position: 'absolute',
					top: '50%',
					left: '50%',
					transform: 'translate(-50%, -50%)',
					width: 600,
					height: 'calc(100% - 200px)',
					bgcolor: 'white',
					boxShadow: 24,
				}}
			>
				<Box
					sx={{
						bgcolor: 'blue',
						color: 'white',
						p: 3,
					}}
				>
					<Typography
						id="modal-modal-title"
						variant="h5"
						component="h2"
						align="center"
						sx={{ fontWeight: 'bolder' }}
					>
						Thm S Kin Mi
					</Typography>
				</Box>
				<Box
					component="form"
					onSubmit={handleSubmit(handleAddNewEvent)}
					sx={{
						p: 3,
					}}
				>
					<Grid container spacing={3}>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.name ? true : false}
								helperText={errors.name && 'Vui lng nhp tn s kin'}
								type="text"
								label="Tn s kin*"
								variant="standard"
								{...register('name', { required: true })}
							/>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.location ? true : false}
								helperText={
									errors.location && 'Vui lng nhp a im t chc s kin'
								}
								type="text"
								label="a im t chc*"
								variant="standard"
								{...register('location', { required: true })}
							/>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<LocalizationProvider dateAdapter={AdapterDateFns} locale={enLocale}>
								<DatePicker
									label="Chn ngy t chc s kin*"
									mask="__/__/____"
									value={dateModal}
									defaultCalendarMonth
									onChange={(newDate) => {
										setDateModal(newDate);
										setValue('dateEvent', newDate);
									}}
									renderInput={(params) => <TextField {...params} />}
								/>
							</LocalizationProvider>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.typeEvent ? true : false}
								helperText={errors.typeEvent && 'Vui lng chn loi s kin'}
								select
								label="Loi s kin*"
								value={typeEvent}
								{...register('typeEvent', { required: true })}
								onChange={(event) => {
									setTypeEvent(event.target.value);
								}}
							>
								<MenuItem value={'public'}>S Kin Cng Khai</MenuItem>
								<MenuItem value={'private'}>S Kin Ring</MenuItem>
								<MenuItem value={'restricted'}>S Kin Hn Ch</MenuItem>
							</TextField>
						</Grid>

						{typeEvent === 'restricted' && (
							<>
								<Grid item xs={12} md={12} sm={12} lg={12}>
									<LocalizationProvider
										dateAdapter={AdapterDateFns}
										locale={enLocale}
									>
										<DatePicker
											label="Chn ngy m ng k*"
											mask="__/__/____"
											value={dateOpenRegModal}
											onChange={(newDate) => {
												setDateOpenRegModal(newDate);
												setValue('openReg', newDate);
											}}
											renderInput={(params) => <TextField {...params} />}
										/>
									</LocalizationProvider>
								</Grid>
								<Grid item xs={12} md={12} sm={12} lg={12}>
									<LocalizationProvider
										dateAdapter={AdapterDateFns}
										locale={enLocale}
									>
										<DatePicker
											label="Chn ngy kt thc ng k*"
											mask="__/__/____"
											value={dateEndRegModal}
											onChange={(newDate) => {
												setDateEndRegModal(newDate);
												setValue('endReg', newDate);
											}}
											renderInput={(params) => <TextField {...params} />}
										/>
									</LocalizationProvider>
								</Grid>
							</>
						)}
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<FormControl component="fieldset">
								<FormLabel component="legend">Lp li: </FormLabel>
								<RadioGroup
									row
									aria-label="repeat-event"
									name="controlled-radio-buttons-reapeat-event"
									value={repeatEvent}
									onChange={(e) => {
										setRepeatEvent(e.target.value);
										setValue('repeatEvent', e.target.value);
									}}
								>
									<FormControlLabel
										value={1}
										control={<Radio />}
										label="Hng ngy"
									/>
									<FormControlLabel
										value={7}
										control={<Radio />}
										label="Hng tun"
									/>
									<FormControlLabel
										value={30}
										control={<Radio />}
										label="Hng thng"
									/>
									<FormControlLabel value={0} control={<Radio />} label="None" />
								</RadioGroup>
							</FormControl>
						</Grid>
					</Grid>
					<Box
						sx={{
							p: 3,
							display: 'flex',
							justifyContent: 'flex-end',
						}}
					>
						<Button variant="contained" color="success" type="submit">
							Thm
						</Button>

						<Button
							variant="contained"
							color="error"
							onClick={handleClose}
							sx={{ ml: 3 }}
						>
							Hy
						</Button>
					</Box>
				</Box>
			</Box>
		</Modal>
	);
}
